.headerContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.appLogo {
    height: 60px;
    pointer-events: none;
}

.appName {
    color: white;
    font-weight: 500;
    font-size: 1.7rem;
}

.right-box-container {
	display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}

.actions-container {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.margin-horizontal-16 {
	margin-left: 1rem;
	margin-right: 1rem;
}
